@import url(https://fonts.googleapis.com/css?family=Assistant:300,400&display=swap);
.App, p {
  text-align: center;
  font-family: 'Assistant', sans-serif!important;
}


.steps{width:100%;height:60px;overflow:hidden}.label{position:relative;top:-53px;left:-40px;font-size:11px}.form{position:absolute;right:4px;top:270px}.form button{margin-top:15px}.first-line{width:100px;height:3px;background-color:#22c1c6;position:absolute;top:40px;left:90px}.MuiStepLabel-active .label,.MuiStepLabel-completed .label{color:#22c1c6}.input{cursor:pointer}

.menu{position:absolute;left:0;top:0;border:1px solid #dbdbdb;height:100vh;z-index:1;background:white}.menu__points{margin:0;color:#777573;margin-top:23px;min-width:50px}.menu__points-wrap{margin-top:-5px}.menu__points-disabled{display:none}.menu__collapse{display:flex;width:127px;margin-top:17px;-webkit-transition:width 0.3s;transition:width 0.3s}.menu__icons-wrap{width:37px}.menu__collapse-collapsed{width:20px}.menu__scaled{-webkit-transform:scaleX(-1);transform:scaleX(-1)}.menu__collapse-button{float:right;margin:3px;-webkit-transition:-webkit-transform 0.1s;transition:-webkit-transform 0.1s;transition:transform 0.1s;transition:transform 0.1s, -webkit-transform 0.1s}.menu__collapse-button:hover{cursor:pointer}.menu__icon-wrapper-collapsed{border-right:2px solid #e98036;width:133px;text-align:left;padding:15px 1px 0 11px;height:25px;margin-top:3px}.menu__icon-wrapper:nth-child(1){border-right:2px solid #e98036;width:30px}.menu__icon-wrapper{display:flex;justify-content:center;align-items:center;margin:13px 6px 0px 6px;padding:1px;width:28px;height:26px}.MuiButton-containedPrimary{width:170px}

